.blur {
    backdrop-filter: blur(5px);
}

.row__modal {
    border: 1px dashed rgba(0, 0, 0, 0.15);
}

.row__text {
    display: flex;
    padding: 16px 48px;
    justify-content: space-between;
    align-items: flex-start;
    align-self: stretch;
}

.filter__text {
    color: #132FBA;
    font-family: 'Tilda Sans', sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%; /* 33.6px */
}

.row__filters {
    display: flex;
    padding: 24px 48px 48px 48px;
    flex-direction: column;
    align-items: flex-start;
    gap: 48px;
    align-self: stretch;
}

.column {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
    align-self: stretch;
}

.categories {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: stretch;
}

.text__wrapper {
    display: flex;
    padding: 8px 0;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    align-self: stretch;

    color: #000;
    font-family: 'Tilda Sans', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */
}

.filters {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    align-self: stretch;
}

.filter__one {
