}

.text__head {
    display: flex;
    padding: 20px 0;
    align-items: center;
    align-self: stretch;

    border-top: 1px solid #000;

    font-family: 'Tilda Sans', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 27px */

    color: #000;

    width: 100%;
}

