label {
    margin: 0 0 0 0;
}

.search__input {
    display: flex;
    width: auto;
    padding: 13px 0 13px 13px ;
    align-items: center;
    gap: 5px;

    border-radius: 15px;
    border: 1px solid #799CF0;
    background: #FFF;

    margin: 0 0 20px;
}

.text__input {
    color: #799CF0;
    font-family: 'Tilda Sans', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;

    border: none;
}

.row__modal__footer {
    display: flex;
    padding: 12px 48px;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
}

.text__input:focus {
    outline: none;
}

.delete__info {
    color: #132FBA;
    font-family: 'Tilda Sans', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */

    margin: 0 0 0 0;
}

.btn__ok {
    color: #FFF;
    font-family: 'Tilda Sans', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */

    display: flex;
    padding: 12px 24px;
    justify-content: center;
    align-items: center;
    gap: 8px;

    border-radius: 20px;
    background: #132FBA;

    border: none;
}

.btn__ok:focus {
    border: none;
}

.input__new {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.input__new+label {
    display: inline-flex;
    align-items: center;
    user-select: none;
}
.input__new+label::before {
    content: '';
    display: inline-block;
    width: 1em;
    height: 1em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #adb5bd;
    border-radius: 0.25em;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
}

.input__new:checked+label::before {
    background-color: #132FBA;
    stroke-width: 1px;
    border-color: #132FBA;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

.search__box {
    padding: 0 0 0 0;
    gap: 32px;
}

.btn__search {
    color: #000;
    font-family: 'Tilda Sans', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */

    display: flex;
    padding: 12px 24px;
    justify-content: center;
    align-items: center;
    gap: 8px;

    border-radius: 20px;
    border: none;
    background: #FFF6D2;
}